.button {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;

  border: none;
  color: $button-color;
  background-color: $accent-color;

  transition: 500ms;
  cursor: pointer;

  &:hover {
    background-color: $accent-color-dark;
  }
}
